@import url("https://fonts.googleapis.com/css?family=Roboto:300,300i,400,400i,500");
@import url("https://fonts.googleapis.com/css?family=Yanone+Kaffeesatz:300,400,700");
@import url("https://fonts.googleapis.com/css?family=Slabo+27px");
.main-page .main-contact {
  background-color: rgba(22, 93, 122, 0.8);
  border-bottom: 2px solid #fff;
  padding-top: 100px;
  padding-bottom: 100px;
}

.main-page .main-contact .header {
  margin: 0px 0px 30px 0px;
  color: #fff;
  text-align: center;
  font-size: 40px;
  font-family: "Roboto", sans-serif;
  font-weight: 200;
}

.main-page .main-contact .contact-container {
  font-family: 'Slabo 27px', serif;
  text-align: center;
}

.main-page .main-contact .contact-container .contact-link {
  text-decoration: none;
  font-size: 21px;
  font-weight: 300;
  border: 2px solid #fff;
  color: #fff;
  border-radius: 3px;
  padding: 7px 20px;
  transition: all .25s;
}

.main-page .main-contact .contact-container .contact-link:hover {
  background-color: #fff;
  color: rgba(22, 93, 122, 0.8);
}

/* Large Devices, Wide Screens */
/* Medium Devices, Desktops */
/* Small Devices, Tablets */
@media only screen and (max-width: 767px) and (min-width: 480px) {
  .main-page .main-contact .header {
    font-size: 35px;
  }
}

/* Extra Small Devices, Phones */
@media only screen and (max-width: 480px) and (min-width: 320px) {
  .main-page .main-contact .header {
    font-size: 35px;
  }
}

/* Custom, iPhone Retina */
@media only screen and (max-width: 320px) {
  .main-page .main-contact .header {
    font-size: 35px;
  }
}
