@import "variables/_variables.sass";

.spinner {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  box-sizing: border-box;
  background-clip: padding-box;
  width: 200px;
  height: 200px;
  border-radius: 100px;
  border: 4px solid rgba(255, 255, 255, 0.1);
  -webkit-mask: linear-gradient(rgba(0, 0, 0, 0.1), #000000 90%);
  transform-origin: 50% 60%;
  transform: perspective(200px) rotateX(66deg);
  animation: spinner-wiggle 1.2s infinite;
  &:before, &:after {
    content: "";
    position: absolute;
    margin: -4px;
    box-sizing: inherit;
    width: inherit;
    height: inherit;
    border-radius: inherit;
    opacity: .05;
    border: inherit;
    border-color: transparent;
    animation: spinner-spin 1.2s cubic-bezier(0.6, 0.2, 0, 0.8) infinite, spinner-fade 1.2s linear infinite; }
  &:before {
    border-top-color: $primary-color; }
  &:after {
    border-top-color: $primary-accent-color;
    animation-delay: 0.3s; } }

@keyframes spinner-wiggle {
  30% {
    transform: perspective(200px) rotateX(66deg); }
  40% {
    transform: perspective(200px) rotateX(65deg); }
  50% {
    transform: perspective(200px) rotateX(68deg); }
  60% {
    transform: perspective(200px) rotateX(64deg); } }

@keyframes spinner-spin {
  100% {
    transform: rotate(360deg); } }

@keyframes spinner-fade {
  20% {
    opacity: .1; }
  40% {
    opacity: 1; }
  60% {
    opacity: .1; } }
