@import url("https://fonts.googleapis.com/css?family=Roboto:300,300i,400,400i,500");
@import url("https://fonts.googleapis.com/css?family=Yanone+Kaffeesatz:300,400,700");
@import url("https://fonts.googleapis.com/css?family=Slabo+27px");
.main-page .main-aboutme {
  padding: 100px 0px 100px 0px;
}

.main-page .main-aboutme .aboutme-row {
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -ms-flex-align: center;
  -webkit-align-items: center;
  -webkit-box-align: center;
  align-items: center;
}

.main-page .main-aboutme .aboutme-row .aboutme-text {
  padding: 0px 50px;
}

.main-page .main-aboutme .aboutme-row .aboutme-text .header {
  font-family: 'Slabo 27px', serif;
  color: #666;
  text-align: left;
  font-weight: 300;
  font-size: 40px;
  margin: 0px 0px 15px 0px;
}

.main-page .main-aboutme .aboutme-row .aboutme-text .description {
  font-family: 'Slabo 27px', serif;
  letter-spacing: 2px;
  text-align: left;
  font-weight: 300;
  font-size: 21px;
  margin-bottom: 0px;
}

.main-page .main-aboutme .aboutme-row .desktop-img-row {
  padding: 0px 50px;
}

.main-page .main-aboutme .aboutme-row .desktop-img-row .img-container {
  border: 2px solid #34b3a0;
  overflow: hidden;
}

.main-page .main-aboutme .aboutme-row .mobile-img-row {
  padding: 0px 50px;
}

.main-page .main-aboutme .aboutme-row .mobile-img-row img {
  margin: 0 auto;
  border: 2px solid #34b3a0;
  height: 225px;
  margin-bottom: 25px;
}

/* Large Devices, Wide Screens */
@media only screen and (max-width: 1200px) and (min-width: 992px) {
  .main-page .main-aboutme {
    padding: 85px 0px 85px 0px;
  }
  .main-page .main-aboutme .aboutme-row .aboutme-text {
    padding: 0px 50px;
  }
  .main-page .main-aboutme .aboutme-row .aboutme-text .header {
    font-size: 38px;
  }
  .main-page .main-aboutme .aboutme-row .aboutme-text .description {
    font-size: 20px;
  }
  .main-page .main-aboutme .aboutme-row .img-row {
    padding: 0px 50px;
  }
}

/* Medium Devices, Desktops */
@media only screen and (max-width: 992px) and (min-width: 767px) {
  .main-page .main-aboutme {
    padding: 70px 0px 70px 0px;
  }
  .main-page .main-aboutme .aboutme-row .aboutme-text {
    padding: 0px 50px;
  }
  .main-page .main-aboutme .aboutme-row .aboutme-text .header {
    font-size: 36px;
  }
  .main-page .main-aboutme .aboutme-row .aboutme-text .description {
    font-size: 19px;
  }
  .main-page .main-aboutme .aboutme-row .img-row {
    padding: 0px 50px;
  }
}

/* Small Devices, Tablets */
@media only screen and (max-width: 767px) and (min-width: 480px) {
  .main-page .main-aboutme {
    padding: 55px 0px 55px 0px;
  }
  .main-page .main-aboutme .aboutme-row {
    display: block;
    -ms-flex-align: default;
    -webkit-align-items: default;
    -webkit-box-align: default;
    align-items: default;
  }
  .main-page .main-aboutme .aboutme-row .aboutme-text {
    padding: 0px 50px;
  }
  .main-page .main-aboutme .aboutme-row .aboutme-text .header {
    text-align: center;
    font-size: 34px;
  }
  .main-page .main-aboutme .aboutme-row .aboutme-text .description {
    text-align: center;
    font-size: 18px;
  }
  .main-page .main-aboutme .aboutme-row .mobile-img-row {
    padding: 0px;
  }
  .main-page .main-aboutme .aboutme-row .mobile-img-row img {
    height: 200px;
  }
}

/* Extra Small Devices, Phones */
@media only screen and (max-width: 480px) and (min-width: 320px) {
  .main-page .main-aboutme {
    padding: 40px 0px 40px 0px;
  }
  .main-page .main-aboutme .aboutme-row {
    display: block;
    -ms-flex-align: default;
    -webkit-align-items: default;
    -webkit-box-align: default;
    align-items: default;
  }
  .main-page .main-aboutme .aboutme-row .aboutme-text {
    padding: 0px 15px;
  }
  .main-page .main-aboutme .aboutme-row .aboutme-text .header {
    text-align: center;
    font-size: 32px;
  }
  .main-page .main-aboutme .aboutme-row .aboutme-text .description {
    text-align: center;
    font-size: 17px;
  }
  .main-page .main-aboutme .aboutme-row .mobile-img-row {
    padding: 0px;
  }
  .main-page .main-aboutme .aboutme-row .mobile-img-row img {
    height: 200px;
  }
}

/* Custom, iPhone Retina */
@media only screen and (max-width: 320px) {
  .main-page .main-aboutme {
    padding: 25px 0px 25px 0px;
  }
  .main-page .main-aboutme .aboutme-row {
    display: block;
    -ms-flex-align: default;
    -webkit-align-items: default;
    -webkit-box-align: default;
    align-items: default;
  }
  .main-page .main-aboutme .aboutme-row .aboutme-text {
    padding: 0px;
  }
  .main-page .main-aboutme .aboutme-row .aboutme-text .header {
    text-align: center;
    font-size: 30px;
  }
  .main-page .main-aboutme .aboutme-row .aboutme-text .description {
    text-align: center;
    font-size: 16px;
  }
  .main-page .main-aboutme .aboutme-row .mobile-img-row {
    padding: 0px;
  }
  .main-page .main-aboutme .aboutme-row .mobile-img-row img {
    height: 200px;
  }
}
